#Register {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fff;
    form {
        width: 90%;
        max-width: 900px;
        box-shadow: none;

        .input-div {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 1em;   
            input {
                margin: 0;
                &:required {
                    border-color: var(--green);
                }
            }         
        }
        .address-div {
            display: grid;
            grid-template-columns: 1fr 9fr;
        }
        input[type="submit"] {
            margin: .5em;
            text-transform: uppercase;
        }
    }
}